<template>
  <div class="getpassword">
    <mu-appbar title="" class='header'>
      <router-link to='/' slot='left'>
        <mu-icon-button icon='keyboard_arrow_left' style='color: #fff'/>
      </router-link>
    </mu-appbar>
    <div>
      <mu-text-field label="" hintText="" v-model="userinfo.userName">
      </mu-text-field>
      <br/>
      <mu-select-field v-if='getType === "tel"' v-model='userinfo.userType' label=''>
        <mu-menu-item value='producter' title=''/>
        <mu-menu-item value='user' title=''/>
      </mu-select-field><br/>
      <mu-text-field class="verify-text-field" v-model="userinfo.verificationCode" hintText=""/>
      <mu-raised-button :label="countDown" primary @click='getTelCode' :disabled='disabled'/>
      <mu-raised-button label="" class="next-btn" primary @click="verify"/>
    </div>
    <mu-dialog :open='resetPwdDialog' title='' @close='closeDialog'>
      <mu-text-field v-model="userinfo.password" hintText="" type='password'>
      </mu-text-field>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='resetPwd' primary label=''/>
    </mu-dialog>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        userinfo: {
          userType: '',
          userName: '',
          password: '',
          verificationCode: ''
        },
        alertDialog: false,
        alertText: '',
        getType: 'tel',
        count: 0,
        disabled: false,
        resetPwdDialog: false
      }
    },
    created() {
      this.getType = this.$route.params.type;
    },
    computed: {
      countDown() {
        return this.count > 0 ? this.count + 's' : '';
      }
    },
    methods: {
      closeDialog(){
        this.resetPwdDialog = false;
      },
      //
      getTelCode() {
        //
        let postObj = {};
        postObj.userName = this.userinfo.userName;
        postObj.userType = this.userinfo.userType;
        this.$store.dispatch('forgetPassword', JSON.stringify(postObj)).then(() => {
          console.log("!");
        }).catch(err => {
          this.alertDialog = true;
          this.alertText = this.$store.state.user.error.errorMessage;
        });
        //
        this.disabled = true;
        this.count = 60;
        setTimeout(this.timer, 0);
      },
      //
      timer() {
        if (this.count > 0) {
          this.count--;
          setTimeout(this.timer, 1000);
        } else {
          this.disabled = false;
        }
      },
      verify(){
        let userInputCode = this.userinfo.verificationCode;
        let serviceBackCode = this.$store.state.user.verificationCode;
        if (userInputCode === serviceBackCode && userInputCode !== '' && serviceBackCode !== '') {
          //
          this.resetPwdDialog = true;
        } else {
          this.alertDialog = true;
          this.alertText = '';
        }
      },
      resetPwd(){
        let postObj = {};
        postObj.userName = this.userinfo.userName;
        postObj.userType = this.userinfo.userType;
        postObj.password = this.userinfo.password;
        this.$store.dispatch('updatePassword', postObj).then(() => {
          this.resetPwdDialog = false;
          console.log("");
          this.$router.push("/");
        }).catch(err => {
          this.alertDialog = true;
          this.alertText = this.$store.state.user.error.errorMessage;
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .getpassword {
    .verify-text-field {
      width: 154px;
    }
  }

  .next-btn {
    display: block;
    margin: 10px auto;
  }
</style>



