<template>
  <div class="friends">
    <mu-appbar title="" class='header'>
      <mu-icon-button icon="add" slot="right" ref='button' @click='toggle'/>
    </mu-appbar>
    <mu-popover :open='openMenu' :autoPosition='false' :trigger='trigger' :anchorOrigin="anchorOrigin"
                :targetOrigin="targetOrigin" @close="handleClose">
      <mu-menu>
        <mu-menu-item title='' @click='openAddGroupDialog' leftIcon="group_add"/>
        <mu-menu-item title='' @click='openAddFriendDialog' leftIcon="person_add"/>
      </mu-menu>
    </mu-popover>
    <mu-list>
      <mu-list-item title="" class='list-item' @click='gotoNewFriends'>
        <mu-icon slot="left" value="person_add"/>
      </mu-list-item>
      <mu-list-item v-for='(item,friendGroupIndex) in friendList' :key='item.groupName' :title='item.groupName'
                    :open='false' toggleNested>
        <mu-icon-menu slot="left" icon="people">
          <mu-menu-item title="" @click="openDeleteFrinedGroupDialog(friendGroupIndex)"/>
        </mu-icon-menu>
        <mu-list-item v-for='(sub,friendIndex) in item.friends' :key='sub.friendName' :title='sub.friendName'
                      slot='nested' @click="gotoFriendInfor">
          <mu-icon-menu slot="left" icon="people">
            <mu-menu-item title="" @click="openMoveFrinedDialog(friendGroupIndex,friendIndex)"/>
            <mu-menu-item title="" @click="openDeleteFrinedDialog(friendGroupIndex,friendIndex)"/>
          </mu-icon-menu>
        </mu-list-item>
      </mu-list-item>
    </mu-list>
    <!-- add friend group dialog -->
    <mu-dialog :open='showAddFriend' title=''>
      <mu-text-field hintText='' v-model='friendName'></mu-text-field>
      <br/>
      <mu-select-field v-model='userType' label=''>
        <mu-menu-item value='producter' title=''/>
        <mu-menu-item value='user' title=''/>
      </mu-select-field>
      <br/>
      <mu-flat-button slot='actions' @click='closeAllDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='addFriend' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='showAddGroup' title=''>
      <mu-text-field hintText='' v-model='addGroupName'></mu-text-field>
      <mu-flat-button slot='actions' @click='closeAllDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='addFriendGroup' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='delFriendGroupDialog' title=''>
      {{ friendGroup.friendGroupName }}
      <mu-flat-button slot='actions' @click='closeAllDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='delFriendGroup' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='delFriendDialog' title=''>
      {{ friendInfo.friendName }}
      <mu-flat-button slot='actions' @click='closeAllDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='delFriend' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='moveFriendDialog' title=''>
      {{ friendInfo.friendName }} <br/>
      <mu-select-field v-model="friendGroupName">
        <mu-menu-item v-for='(item,index) in friendList' :key='index' :value='item.groupName' :title='item.groupName'/>
      </mu-select-field>
      <mu-flat-button slot='actions' @click='closeAllDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='moveFriend' primary label=''/>
    </mu-dialog>
    <mu-toast v-if="toastFlag" :message="toastMsg" @close="hideToast"/>
    <transition name='router-show'>
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        /**
         * 
         */
        /**/
        toastFlag: '',
        /**/
        showAddGroup: false,
        showAddFriend: false,
        delFriendGroupDialog: false,
        moveFriendDialog: false,
        delFriendDialog: false,
        /**/
        openMenu: false,
        trigger: null,
        anchorOrigin: {
          vertical: 'bottom',
          horizontal: 'right'
        },
        targetOrigin: {
          vertical: 'top',
          horizontal: 'right'
        },
        /**
         * v-model
         */
        //
        addGroupName: '',
        //
        friendName: '',
        userType: '',
        //
        friendGroupName: '',
        /**
         * 
         */
        /**/
        toastMsg: '',
        //
        friendGroup: {
          friendGroupName: '',
          friendGroupId: ''
        },
        //
        friendInfo: {
          friendName: '',
          userType: '',
          groupName: ''
        },
      }
    },
    created () {
      /**
       * 
       */
      this.$store.dispatch('getFriends');
    },
    mounted () {
      this.trigger = this.$refs.button.$el;
    },
    computed: {
      friendList() {
        return this.$store.state.friend.friendList;
      }
    },
    methods: {
      /**
       * 
       */
      /**/
      showToast (toastMsg) {
        this.toastMsg = toastMsg;
        this.toastFlag = true;
        if (this.toastTimer) clearTimeout(this.toastTimer);
        this.toastTimer = setTimeout(() => {
          this.toastFlag = false;
        }, 2000)
      },
      hideToast () {
        this.toastFlag = false;
        if (this.toastTimer) clearTimeout(this.toastTimer);
      },
      /**/
      openAddGroupDialog () {
        this.addGroupName = '';
        this.showAddGroup = true;
      },
      openDeleteFrinedGroupDialog(friendGroupIndex){
        this.friendGroup.friendGroupId = this.$store.state.friend.friendList[friendGroupIndex].groupId;
        this.friendGroup.friendGroupName = this.$store.state.friend.friendList[friendGroupIndex].groupName;
        this.delFriendGroupDialog = true;
      },
      openAddFriendDialog() {
        this.showAddFriend = true;
      },
      openMoveFrinedDialog(friendGroupIndex, friendIndex){
        this.friendInfo.friendName = this.$store.state.friend.friendList[friendGroupIndex].friends[friendIndex].friendName;
        this.friendInfo.userType = this.$store.state.friend.friendList[friendGroupIndex].friends[friendIndex].userType;
        this.moveFriendDialog = true;
      },
      openDeleteFrinedDialog(friendGroupIndex, friendIndex){
        this.friendInfo.friendName = this.$store.state.friend.friendList[friendGroupIndex].friends[friendIndex].friendName;
        this.friendInfo.userType = this.$store.state.friend.friendList[friendGroupIndex].friends[friendIndex].userType;
        this.delFriendDialog = true;
      },
      closeAllDialog() {
        this.showAddGroup = false;
        this.showAddFriend = false;
        this.delFriendGroupDialog = false;
        this.moveFriendDialog = false;
        this.delFriendDialog = false;
      },
      /**/
      toggle () {
        this.openMenu = !this.openMenu;
      },
      handleClose () {
        this.openMenu = false;
      },
      /**
       * 
       */
      gotoFriendInfor(){
//        this.$router.push('/friends/infor');
      },
      gotoNewFriends() {
        this.$store.dispatch('getUnreceivedList').then(() => {
          this.$router.push('/friends/newfriends');
        }).catch(err => {
          console.log(err);
        });
      },
      /**
       * store
       */
      /**/
      addFriendGroup() {
        if (this.addGroupName) {
          let postObj = {};
          postObj.friendGroupName = this.addGroupName;
          this.$store.dispatch('addFriendGroup', postObj).then(() => {
            this.showAddGroup = false;
            this.showToast('');
            this.$store.dispatch('getFriends')
          }).catch(err => {
            this.showToast(err.message);
            console.log(err);
          });
        }
      },
      addFriend() {
        let postObj = {};
        postObj.friendName = this.friendName;
        postObj.userType = this.userType;
        this.$store.dispatch('addFriend', postObj).then(() => {
          this.showAddFriend = false;
          this.showToast('');
          this.$store.dispatch('getFriends');
        }).catch(err => {
          this.showToast(err.message);
          console.log(err);
        });
      },
      moveFriend(){
        let postObj = {};
        postObj.friendName = this.friendInfo.friendName;
        postObj.friendType = this.friendInfo.userType;
        postObj.bemoveGroupName = this.friendgroup.friendGroupName;
        this.$store.dispatch('updateFriendGroup', postObj).then(() => {
          this.moveFriendDialog = false;
          this.showToast('');
          this.$store.dispatch('getFriends');
        }).catch(err => {
          this.showToast(err.message);
          console.log(err);
        });
      },
      delFriend(){
        let postObj = {};
        postObj.friendName = this.friendInfo.friendName;
        postObj.friendType = this.friendInfo.userType;
        this.$store.dispatch('deleteFriend', postObj).then(() => {
          this.delFriendDialog = false;
          this.showToast('');
          this.$store.dispatch('getFriends');
        }).catch(err => {
          this.showToast(err.message);
          console.log(err);
        });
      },
      delFriendGroup(){
        let postObj = {};
        postObj.friendGroupId = this.friendGroup.friendGroupId;
        this.$store.dispatch('delFriendGroup', postObj).then(() => {
          this.delFriendGroupDialog = false;
          this.showToast('');
          this.$store.dispatch('getFriends');
        }).catch(err => {
          this.showToast(err.message);
          console.log(err);
        });
      }
    }
  }
</script>

<style lang='scss' scoped>
  .friends {
    margin: 0 5px;
    .list-item {
      border-bottom: 1px solid lightgray;
    }
  }
</style>



