<template>
  <div class="add-device">
    <mu-appbar :title='productInfo.productName' class='header'>
      <mu-icon-button slot='left' icon='keyboard_arrow_left' style='color: #fff' @click="this.history.back()"/>
    </mu-appbar>
    <div class="page-part">
      <mu-text-field label="" hintText="" v-model="deviceInfo.deviceAlias"></mu-text-field>
      <br/>
      <mu-text-field label="" hintText="" v-model="deviceInfo.deviceName"></mu-text-field>
      <br/>
    </div>
    <mu-raised-button label="" class="demo-raised-button" primary @click='registerDevice'/>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        /**
         * v-model
         */
        deviceInfo: {
          deviceAlias: '',
          deviceName: ''
        },
        /**
         * store
         */
        productInfo: {}
      }
    },
    created(){
      /**
       * 
       */
      //
      this.productInfo = this.$store.state.devices.productInfo;
    },
    methods: {
      /**
       * store
       */
       /**/
      registerDevice() {
        let postObj = {
          productKey: this.productInfo.productKey,
          deviceLists: [
            {
              deviceName: this.deviceInfo.deviceName,
              deviceAlias: this.deviceInfo.deviceAlias
            }
          ]
        };
        this.$store.dispatch('registerDevice', postObj).then(() => {
          this.$router.go(-1);
          this.$store.dispatch('getProducts');
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .select {
    text-align: left;
  }

  .add-device {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    background-color: #fff;
    .page-part {
      padding-top: 62px;
    }
  }
</style>



