<template>
  <div class="register">
    <mu-appbar title='' class='header'>
      <mu-icon-button slot='left' icon='keyboard_arrow_left' style='color: #fff' @click="this.history.back()"/>
    </mu-appbar>
    <div class="page-part">
      <mu-text-field label="" hintText="" v-model="userinfo.userName"></mu-text-field>
      <br/>
      <mu-text-field label="" hintText="" type="password" v-model="userinfo.password"></mu-text-field>
      <br/>
      <mu-text-field label="" hintText="" type="password" v-model="retypePwd"></mu-text-field>
      <br/>
      <div class="address">
        <mu-select-field v-model='userinfo.country' label=''>
          <mu-menu-item v-for='text, index in countryList' :key='index' :value='text' :title='text'/>
        </mu-select-field>
        <mu-select-field v-model='userinfo.province' label=''>
          <mu-menu-item v-for='item,index in provinceList' :key='item' :value='item.name' :title='item.name'/>
        </mu-select-field>
      </div>
      <mu-text-field label="" hintText="" v-model="userinfo.tel"></mu-text-field>
      <br/>
      <mu-text-field label="" hintText="" type="email" v-model="userinfo.email"></mu-text-field>
      <br/>
      <mu-text-field label="" hintText="" v-model="userinfo.company"></mu-text-field>
      <br/>
      <mu-select-field v-model='userinfo.userType' label=''>
        <mu-menu-item value='producter' title=''/>
        <mu-menu-item value='user' title=''/>
      </mu-select-field>
    </div>
    <mu-raised-button label="" class="demo-raised-button" primary @click="userRegister"/>
    <br/><br/><br/>
    <mu-dialog :open="alertDialog" v-if="alertDialog" :showHide="alertDialog" title="">
      <p>{{ alertText }}</p>
      <mu-flat-button label="" slot="actions" primary @click="closeDialog"/>
    </mu-dialog>
    <mu-dialog :open='verifyTelDialog'>
      <mu-text-field label="" hintText="" v-model="verificationCode"></mu-text-field>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='verifyTel' primary label=''/>
    </mu-dialog>
  </div>
</template>

<script>
  import {regions} from '@/utils/region'
  import {checkUserName, checkEmail, checkPhoneNum} from '@/utils/validate'
  export default {
    name: 'register',
    data () {
      return {
        /**
         * 
         */
        alertDialog: false,
        verifyTelDialog: false,
        /**
         * v-model
         */
        userinfo: {
          userName: '',
          password: '',
          country: '',
          province: '',
          company: '',
          tel: '',
          email: '',
          userType: ''
        },
        retypePwd: '',
        /**
         * 
         */
        //
        alertText: '',
        countryList: [''],
        provinceList: [],
        verificationCode: ''
      }
    },
    created(){
      //data
      this.provinceList = regions;
      this.userinfo.country = '';
      this.userinfo.province = '';
    },
    methods: {
      /**
       * 
       */
      closeDialog() {
        this.alertDialog = false;
        this.verifyTelDialog = false;
      },
      /**
       * store
       */
      //
      userRegister() {
        if (!this.checkUserInput()) {
          this.alertDialog = true;
          return;
        }
        //
        let postObj = {};
        postObj.userType = this.userinfo.userType;
        postObj.userName = this.userinfo.userName;
        this.$store.dispatch('isUserExist', postObj).then(() => {
          //,
          let postObj = {};
          postObj.tel = this.userinfo.tel;
          this.$store.dispatch('regist_verify', postObj).then(() => {
            this.verifyTelDialog = true;
          }).catch(err => {
            this.alertDialog = true;
            this.alertText = this.$store.state.user.error.errorMessage;
          });
        }).catch(err => {
          this.alertText = this.$store.state.user.error.errorMessage;
          this.alertDialog = true;
        });
      },
      verifyTel(){
        //
        let userInputCode = this.verificationCode;
        let serviceBackCode = this.$store.state.user.verificationCode;
        if (userInputCode === serviceBackCode && userInputCode !== '' && serviceBackCode !== '') {
          this.$store.dispatch('userRegister', this.userinfo).then(() => {
            this.$router.go(-1);
          }).catch(err => {
            this.alertDialog = true;
            this.alertText = this.$store.state.user.error.errorMessage;
          });
        }
      },
      /**
       * 
       */
      checkUserInput() {
//        if (!checkUserName(this.userinfo.userName)) {
//          this.alertText = '';
//          return;
//        }
        if (this.userinfo.userName.length === 0) {
          this.alertText = '';
          return
        }
        if (this.userinfo.password.length < 6) {
          this.alertText = '6';
          return
        }
        if (this.userinfo.password !== this.retypePwd) {
          this.alertText = ',';
          return;
        }
        if (!checkPhoneNum(this.userinfo.tel)) {
          this.alertText = '';
          return;
        }
        if (!checkEmail(this.userinfo.email)) {
          this.alertText = '';
          return;
        }
        return true;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .regTop {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  }

  .address .mu-select-field {
    padding-left: 0;
    display: inline-block;
    width: 126px;
  }

  .mu-text-field {
    height: 40px;
  }
</style>



