<template>
  <div>
    <h3></h3>
    <!--v-model-->
    <mu-text-field label="" hintText="" v-model="userName"></mu-text-field>
    <br/>
    <mu-text-field label=" " hintText="" type="password" v-model="password"></mu-text-field>
    <br/>
    <mu-select-field v-model='userType' label=''>
      <mu-menu-item value='producter' title=''/>
      <mu-menu-item value='user' title=''/>
    </mu-select-field>
    <br/>
    <mu-raised-button label="" primary @click="userlogin"/>
    <div class="link">
      <mu-flat-button to='/register' label="" class="flat-button" primary/>
      <mu-flat-button label="" class="flat-button" primary @click='openBottomSheet'/>
    </div>
    <mu-dialog :open="alertDialog" v-if="alertDialog" :showHide="alertDialog" title="">
      <p>{{ alertText }}</p>
      <mu-flat-button label="" slot="actions" primary @click="closeAlert"/>
    </mu-dialog>
    <mu-bottom-sheet :open="bottomSheet" @close="closeBottomSheet">
      <mu-list @itemClick="closeBottomSheet">
        <mu-sub-header></mu-sub-header>
        <mu-list-item to='/getpassword/tel' title=""/>
      </mu-list>
    </mu-bottom-sheet>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        /**
         * 
         */
        //
        alertDialog: false,
        bottomSheet: false,
        /**
         * v-model
         */
        userType: '',
        userName: '',
        password: '',
        /**
         * 
         */
        alertText: '',
      }
    },
    methods: {
      /**
       * 
       */
      closeAlert() {
        this.alertDialog = false;
      },
      openBottomSheet() {
        this.bottomSheet = true;
      },
      closeBottomSheet() {
        this.bottomSheet = false;
      },
      /**
       * store
       */
      //
      userlogin() {
        //---
        if (this.password.length <= 0) {
          this.alertDialog = true;
          this.alertText = '';
          return
        }
        let postObj = {};
        postObj.userType = this.userType;
        postObj.userName = this.userName;
        postObj.password = this.password;
        this.$store.dispatch('login', postObj).then(() => {
          this.$router.push({path: '/devices'});
        }).catch(err => {
          this.alertDialog = true;
          this.alertText = this.$store.state.user.error.errorMessage;
        });
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .link {
    margin: 150px auto 0;
    //border: 1px solid #000;
    width: 180px;
    height: 40px;
    .flat-button {
      &:first-child {
        float: left;
      }
      &:last-child {
        float: right;
      }
    }
  }
</style>



