<template>
  <div class="product-list">
    <!--header-->
    <mu-appbar title='' class='header'>
      <router-link to='/devices' slot='left'>
        <mu-icon-button icon='keyboard_arrow_left' style='color: #fff'/>
      </router-link>
      <mu-icon-button icon="add" slot="right" ref='button' @click='openAddProductDialog'/>
    </mu-appbar>
    <div class="page-part">
      <mu-list>
        <mu-list-item v-for='(item,index) in productList' :key='item.productKey' :title='item.productName'
                      :describeText="item.productDesc" :open='false' class='group' toggleNested>
          <mu-icon-menu slot="leftAvatar" icon="settings_system_daydream" tooltip="">
            <mu-menu-item title="" @click='openModifyProductDialog(item)'/>
            <mu-menu-item title="" @click='openDelProductDialog(item)'/>
            <mu-menu-item title='' @click='gotoAddDevice(item)'/>
          </mu-icon-menu>
          <mu-list-item v-for='(sub,deviceIndex) in item.deviceList' :key='sub.deviceId' :title='sub.deviceAlias'
                        :describeText="sub.deviceName" slot='nested'>
            <mu-icon-menu slot="right" icon="more_vert" tooltip="">
              <mu-menu-item v-if='!sub.isSelled' title="" @click='openAllocGroup(sub.deviceId,index,deviceIndex)'/>
              <mu-menu-item title="" @click='openDelDeviceDialog(index,deviceIndex)'/>
            </mu-icon-menu>
          </mu-list-item>
        </mu-list-item>
      </mu-list>
    </div>
    <mu-dialog :open='addProductDialog' title='' @close='closeDialog'>
      <mu-text-field hintText='' v-model='productInfo.productName'></mu-text-field>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='addProduct' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='modifyProductDialog' title='' @close='closeDialog'>
      <mu-text-field label='' v-model='productInfo.productName'></mu-text-field>
      <br/>
      <mu-text-field label='' multiLine :rows="3" :rowsMax="6" v-model='productInfo.productDesc'></mu-text-field>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='updateProduct' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='delProductDialog' title='' @close='closeDialog'>
      <p>: {{productInfo.productName}} ?</p>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='deleteProduct' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='moveToDeviceGroupDialog' title='' @close='closeDialog'>
      <mu-select-field class='select' v-model="updateGroup.deviceGroupId" label="">
        <mu-menu-item v-for='(item,index) in this.$store.state.devices.deviceLists' :key='index'
                      :value='item.deviceGroupId'
                      :title='item.deviceGroupName'/>
      </mu-select-field>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='allocGroup' primary label=''/>
    </mu-dialog>
    <mu-dialog :open='delDeviceDialog' title='' @close='closeDialog'>
      <p>: {{device.deviceAlias}} ?</p>
      <mu-flat-button slot='actions' @click='closeDialog' primary label=''/>
      <mu-flat-button slot='actions' @click='deleteDevice' primary label=''/>
    </mu-dialog>
    <mu-toast v-if="toastFlag" :message="toastMsg" @close="hideToast"/>
    <transition name='router-show'>
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        /**
         * 
         */
        //
        addProductDialog: false,
        modifyProductDialog: false,
        delProductDialog: false,
        moveToDeviceGroupDialog: false,
        delDeviceDialog: false,
        //toast
        toastFlag: false,
        toastTimer: 0,
        toastMsg: '',
        /**
         * store
         */
        device: {
          deviceId: '',
          deviceAlias: ''
        },
        productInfo: {
          productKey: '',
          productName: '',
          productDesc: ''
        },
        updateGroup: {
          deviceId: '',
          deviceGroupId: '',
          shareId: ''
        },
        deviceGroup: ''
      }
    },
    computed: {
      productList() {
        return this.$store.state.devices.productList;
      }
    },
    methods: {
      /**
       * 
       */
      /**/
      openAddProductDialog() {
        this.addProductDialog = true;
      },
      openModifyProductDialog(productInfo) {
        this.productInfo = productInfo;
        this.modifyProductDialog = true;
      },
      openDelProductDialog(productInfo) {
        this.productInfo = productInfo;
        this.delProductDialog = true;
      },
      openDelDeviceDialog(index, deviceIndex,){
        this.device.deviceAlias = this.$store.state.devices.productList[index].deviceList[deviceIndex].deviceAlias;
        this.device.deviceId = this.$store.state.devices.productList[index].deviceList[deviceIndex].deviceId;
        this.delDeviceDialog = true;
      },
      closeDialog() {
        this.addProductDialog = false;
        this.modifyProductDialog = false;
        this.delProductDialog = false;
        this.moveToDeviceGroupDialog = false;
        this.delDeviceDialog = false;
      },
      /**
       * 
       */
      gotoAddDevice(productInfo){
        this.$store.commit('SET_PRODUCTINFO',productInfo);
        this.$router.push('/devices/adddevice');
      },
      handleClose () {
        this.openDialog = false;
      },
      addProduct() {
        this.$store.dispatch('addProduct', this.productInfo.productName).then(() => {
          this.$store.dispatch('getProducts');
          console.log('');
        }).catch(err => {
          console.log('!');
        });
        this.addProductDialog = false;
      },
      updateProduct() {
        this.modifyProductDialog = false;
        this.$store.dispatch('updateProduct', this.productInfo).then(() => {
          this.$store.dispatch('getProducts');
          console.log('');
          this.toastMsg = '';
          this.showToast();
        }).catch(err => {
          console.log('!');
        });
      },
      deleteProduct() {
        this.delProductDialog = false;
        this.$store.dispatch('deleteProduct', this.productInfo.productKey).then(() => {
          this.$store.dispatch('getProducts');
          console.log('');
          this.toastMsg = '';
          this.showToast();
        }).catch(err => {
          console.log('!');
        });
      },
      deleteDevice(){
        this.delDeviceDialog = false;
        this.$store.dispatch('deleteDevice', this.device.deviceId).then(() => {
          this.$store.dispatch('getProducts');
          this.toastMsg = '';
          this.showToast();
        }).catch(err => {
          this.toastMsg = err.message;
          this.showToast();
        });
      },
      openAllocGroup(deviceId, index, deviceIndex) {
        this.$store.state.devices.productListIndex = index;
        this.$store.state.devices.productDeviceListIndex = deviceIndex;
        //postmethods
        this.updateGroup.deviceId = deviceId;
        //
        this.moveToDeviceGroupDialog = true;
      },
      allocGroup() {
        this.moveToDeviceGroupDialog = false;
        this.updateGroup.shareId = this.$store.state.user.userinfo.userId;
        this.$store.dispatch('updateDeviceGroup', JSON.stringify(this.updateGroup)).then(() => {
          //index$storeDOM
          this.$store.commit('SET_ISSEHLLED', true);
          this.toastMsg = '';
          this.showToast();
        }).catch(err => {
            console.log(err)
        });
      },
      showToast () {
        this.toastFlag = true;
        if (this.toastTimer) clearTimeout(this.toastTimer);
        this.toastTimer = setTimeout(() => {
          this.toastFlag = false;
        }, 2000)
      },
      hideToast () {
        this.toastFlag = false;
        if (this.toastTimer) clearTimeout(this.toastTimer);
      }
    }
  }
</script>

<style lang='scss' scoped>
  .router-show-enter-active, .router-show-leave-active {
    transition: all .4s;
  }

  .router-show-enter, .router-show-leave {
    transform: translateX(100%)
  }

  .product-list {
    .page-part {
      text-align: left;
      padding-bottom: 5px;
      .add-group {
        margin-right: 65px;
      }
    }
  }
</style>



